<template>
  <div
    class="ag-grid-table-wrap"
    :style="tableStyle"
    ref="tableContainer"
  >
    <ag-grid-vue
      v-if="columns.length > 0"
      ref="table"
      class="ag-grid-table ag-theme-balham"
      :gridOptions="realAgOptions"
      :modules='modules'
    >
    </ag-grid-vue>
  </div>
</template>

<script type="text/ecmascript-6">
import '../../assets/css/ag-grid/ag-grid.css'
import '../../assets/css/ag-grid/ag-theme-balham.css'
import { AgGridVue } from '@ag-grid-community/vue';
import { ClientSideRowModelModule } from "@ag-grid-community/client-side-row-model";
import { ColumnsToolPanelModule } from '@ag-grid-enterprise/column-tool-panel'
import { LicenseManager } from "@ag-grid-enterprise/core";
import { MenuModule } from '@ag-grid-enterprise/menu'
import { ClipboardModule } from '@ag-grid-enterprise/clipboard'
import { SetFilterModule } from '@ag-grid-enterprise/set-filter'

// import { LicenseManager } from '../../assets/ag-grid-enterprise'
import { agGridEnterpriseLicenseKey, defaultConfig, cssFeatures } from './ag-config'
import deepClone from '../../utils/deepClone'
import { isArray } from '../../utils/array'
import { isFunction } from '../../utils/common'
import { isObject } from '../../utils/object'

import CustomHeader from './renderComponents/CustomHeader.vue'
import CellRender from './CellRender.vue'
import CellRenderByFunction from './CellRenderByFunction.vue'
import TextComponent from './renderComponents/TextComponent.vue'
import SequenceComponent from './renderComponents/SequenceComponent.vue'
import { toThousands } from '../../utils/number'
import i18n from '../../utils/i18n'

LicenseManager.setLicenseKey(agGridEnterpriseLicenseKey);

// tooltip
const tooltipBox = document.createElement('div');
tooltipBox.classList.add(cssFeatures.tooltipBox);

// toolTipTop
const tooltipTopBox = document.createElement('div');
tooltipTopBox.classList.add(cssFeatures.tooltipTopBox);
tooltipTopBox.classList.add('arrow-down');
tooltipTopBox.style.display = 'none';


let updateColumnVisibleDelay = null; // column visible 
let updateColumnPositionDelay = null; // column move 

const Common_Table_Mode = 'commonTable'
const AG_SEQUENCE_COLUMN_NAME = '__ag_sequence_column_name__'

export default {
  name: 'AgGridTable',

  components: {
    AgGridVue,
    CustomHeader,
    CellRender,
    CellRenderByFunction,
    TextComponent,
    SequenceComponent
  },

  props: {
    // ag
    options: {
      type: Object,
      default: () => ({})
    },
    // 
    height: {
      type: String,
    },
    // 
    data: {
      default: () => ([])
    },
    // 
    columns: {
      default: () => ([])
    },
    // 
    renderParams: {
      type: Function
    },
    // commonTable()
    mode: {
      type: String,
      default: ''
    },
    // 
    externalModules: {
      type: Array,
      default: () => ([])
    },
    // 
    sequenceColumnField: {
      type: String,
      default: AG_SEQUENCE_COLUMN_NAME
    },
    // 
    rowDataType: {
      type: String, // {val: xx}
      default: 'object'
    }
  },

  computed: {
    // 
    tableStyle() {
      return this.height ? `height: ${this.height};` : ''
    },

    // ag
    gridOptions() {
      // defaultConfig
      const otherOptions = {
        onGridReady: this.onGridReady,
        getContextMenuItems: this.getContextMenuItems.bind(this),
        getMainMenuItems: this.getMainMenuItems.bind(this),
        onCellClicked: this.onCellClicked,
        onCellDoubleClicked: this.onCellDoubleClicked,
        onRowClicked: this.onRowClicked,
        onRowDoubleClicked: this.onRowDoubleClicked,
        onSelectionChanged: this.onSelectionChanged.bind(this),
        onSortChanged: this.onSortChanged,
        onVirtualColumnsChanged: this.onVirtualColumnsChanged,
        onColumnVisible: this.onColumnVisible,
        onColumnMoved: this.onColumnMoved,
        onColumnPinned: this.onColumnPinned,
        onRowDataChanged: this.onRowDataChanged,
        onBodyScroll: this.onBodyScroll,
        onFilterChanged: this.onFilterChanged,
        onColumnResized: this.onColumnResized,
        getRowClass: this.getRowClass,
        getRowStyle: this.getRowStyle,
        onGridSizeChanged: this.onGridSizeChanged
      }
      const defaultColDef = this.options.defaultColDef || {}
      return Object.assign({}, defaultConfig, otherOptions, this.options, {
        defaultColDef: {
          ...defaultConfig.defaultColDef,
          ...defaultColDef
        }
      })
    },

    // ag
    realAgOptions() {
      const obj = Object.assign({}, this.gridOptions)
      delete obj.cssStatus
      delete obj.defaultSort
      delete obj.datas
      delete obj.agCellSingleClick
      delete obj.agCellDoubleClick
      delete obj.agRowClick
      delete obj.agRowDoubleClick
      delete obj.agSortChanged
      delete obj.agColumnVisibleChanged
      delete obj.agSelectionChanged
      delete obj.agColumnMoved
      delete obj.agColumnPinned
      return obj
    }
  },

  watch: {
    // 
    data: {
      handler(newData) {
        if (isArray(newData) && this._gridReady) {
          // $nextTick   
          this.$nextTick(() => {
            this.setRows(newData)
          })
        }
      },
    },

    // 
    columns: {
      handler(newData) {
        if (isArray(newData) && this._gridReady) {
          this.setCols(newData)
        }
      },
    },
  },

  data() {
    return {
      api: null, // api
      columnApi: null, // api
      subtotalRowData: null, // 
      fullRangeSubTotalRowData: null, // 
      realRows: [], // 
      realColumns: [], //
      modules: [
        ClientSideRowModelModule,
        ColumnsToolPanelModule,
        MenuModule,
        ClipboardModule,
        SetFilterModule,
        ...this.externalModules
      ],
    }
  },

  methods: {
    // 
    onGridReady(params) {
      this.api = params.api
      this.columnApi = params.columnApi

      this._gridReady = true
      const agGridDiv = this.$refs.table.$el
      const agGridTableContainer = this.$refs.tableContainer

      setTimeout(() => {
        // 
        this._horizontalScrollTo(agGridDiv.querySelector('.ag-body-viewport'), agGridTableContainer.getAttribute('data-scroll-left')); // 
        // 
        this._autoSizeColumns()
      }, 20)
      // 
      agGridDiv.appendChild(tooltipBox);
      document.body.appendChild(tooltipTopBox);
      // ag-tool-panel
      // agGridDiv.querySelector('.ag-tool-panel').remove();

      if (isArray(this.columns) && this.columns.length > 0) {
        this.setCols(this.columns)
        if (isArray(this.data) && this.data.length > 0) {
          // $nextTick   
          this.$nextTick(() => {
            this.setRows(this.data)
          })
        }
      }
      this.$emit('grid-ready', params)
    },

    // 
    onGridSizeChanged(params) {
      this.$emit('on-grid-size-changed', params)
      setTimeout(() => {
        const { clientWidth } = params;
        if (clientWidth === this._tableWidthCache) {
          return
        }
        this._tableWidthCache = clientWidth
        this._resetColumnWidth()
      }, 200)
    },

    // 
    setRows(data) {
      const agTable = this.$refs.table
      if (!data) {
        return agTable;
      }

      if (!isArray(data)) {
        alert('agTable.setRows requires Array as first param');
        return agTable;
      }
      const { rowData, pinnedBottomRowData } = this._transformRowData(data);
      // 
      const start = (this.options.datas && this.options.datas.start) || 0
      rowData.forEach((d, i) => {
        d[this.sequenceColumnField] = { val: start + (i + 1) };
      });

      // console.log('', rowData);
      // console.log('', pinnedBottomRowData)
      this._pinnedBottomRowData = pinnedBottomRowData;
      this.realRows = rowData
      this.api.setRowData(rowData);
      this.api.setPinnedBottomRowData(pinnedBottomRowData);
      this._fixAgRenderChoke();
      this.api.hideOverlay();
      return agTable;
    },

    // 
    setCols(data) {
      const agTable = this.$refs.table
      if (!data) {
        return agTable;
      }

      if (!isArray(data)) {
        alert('agTable.setCols requires Array as first param');
        return agTable;
      }


      this._colPosition = '';
      this._originColumnDefs = data;

      const options = this.gridOptions;
      const colPosition = (options.datas && options.datas.colPosition) || '';
      let colData
      // 
      if (colPosition && colPosition !== '') {
        colData = this._getSortedData(data, colPosition)
      } else {
        // 
        if (this.options.datas && this.options.datas.pinnedColumns) {
          this._sortPinnedColumns(data)
        }
        colData = data
      }

      this.realColumns = this._transformColumnDefs(colData)
      this.api.setColumnDefs(this.realColumns);
      // console.log('', this._transformColumnDefs(colData));
      setTimeout(() => {
        this._autoSizeColumns()
      }, 20)
      return agTable;
    },

    // 
    getContextMenuItems(param) {
      return [
        'copy',
        'copyWithHeaders',
        'paste',
        // 'export',
        {
          name: this.$t('table.showAllColumns'),
          action: () => {
            const options = this.options; // 
            // param.columnApi.resetColumnState();
            // 
            param.columnApi.setColumnsVisible(param.columnApi.getColumnState().map(d => d.colId), true);
            setTimeout(() => {
              updateColumnVisibleDelay && clearTimeout(updateColumnVisibleDelay);
            }, 0);
            // 
            if (this._colPosition && this._colPosition !== '') {
              this._colPosition.split(',').forEach((d, i) => {
                param.columnApi.moveColumn(d, i);
              });
              setTimeout(() => {
                updateColumnPositionDelay && clearTimeout(updateColumnPositionDelay);
              }, 0);
            }

            if (isFunction(options.agColumnVisibleChanged)) {
              // API
              this._preventPinnedEmit = true; // 
              options.agColumnVisibleChanged('', () => {
                this._preventPinnedEmit = false;
              });
            }
          },
        },
      ];
    },

    // general menu item
    getMainMenuItems(params) {
      return [
        'pinSubMenu',
        'separator',
        'autoSizeThis',
        'autoSizeAll',
        'separator',
        {
          name: this.$t('table.hideCurrentColumn'),
          action: () => {
            const { columnApi, column } = params
            columnApi.setColumnVisible(column.colId, false)
          }
        },
        {
          name: this.$t('table.resetColumns'),
          action: () => {
            this._isResetAllColumn = true;
            const options = this.options;

            const visibleColumns = [];
            this._originColumnDefs.map(d => d.colname || d.field).forEach((d) => {
              const column = params.columnApi.getColumn(d)
              if (column && column.visible) {
                visibleColumns.push(d);
              }
            });
            visibleColumns.forEach((d, i) => {
              params.columnApi.moveColumn(d, i);
            });
            const pinnedColumns = (this.options.datas && this.options.datas.pinnedColumns) || ''
            const { pinnedLeftColumns, pinnedRightColumns } = this._getPinnedColumns(pinnedColumns)
            const allFixedColumns = params.columnApi.getDisplayedLeftColumns().map(d => d.colId).concat(params.columnApi.getDisplayedRightColumns().map(d => d.colId))

            allFixedColumns.forEach((colId) => {
              if (colId !== 'ID' && !pinnedLeftColumns.includes(colId) && !pinnedRightColumns.includes(colId)) {
                params.columnApi.setColumnPinned(colId, false);
              }
            });
            setTimeout(() => {
              updateColumnPositionDelay && clearTimeout(updateColumnPositionDelay);
            }, 0);
            this._colPosition = '';

            if (isFunction(options.agColumnMoved)) {
              options.agColumnMoved('');
            }

            if (isFunction(options.agColumnPinned)) {
              options.agColumnPinned('ID|')
            }
            setTimeout(() => {
              this._isResetAllColumn = false;
            }, 500);
          },
        },
      ];
    },

    // 
    onCellClicked(params) {
      const { colDef, data, event } = params;
      const { __ag_is_statistic_row__ } = data;
      const options = this.options;
      if (isFunction(options.agCellSingleClick) && !__ag_is_statistic_row__) {
        options.agCellSingleClick(colDef, data, event.target);
      }
      this.$emit('on-cell-single-click', params)

      // web
      // fix: 
      if(window.arkui_BCL_in_mobile) {
        this._cellDoubleClickedInMobile(params)
      }
    },

    // web
    _cellDoubleClickedInMobile(params) {
      if(this._clickTime === undefined) {
        this._clickTime = +new Date()
        return
      }
      const currentTime = +new Date()
      if(currentTime - this._clickTime < 400) {
        this.onCellDoubleClicked(Object.assign(params,{type:'cellDoubleClicked',mockEvent: true})) // mockEvent,
      }
      this._clickTime = +new Date()
    },

    // 
    onCellDoubleClicked(params) {
      const { colDef, data, event } = params;
      const { __ag_is_statistic_row__ } = data;
      const options = this.options;
      if (isFunction(options.agCellDoubleClick) && !__ag_is_statistic_row__) {
        options.agCellDoubleClick(colDef, data, event.target);
      }
      this.$emit('on-cell-double-click', params)
    },

    // 
    onRowClicked(params) {
      const { colDef, data, event } = params;
      const { __ag_is_statistic_row__ } = data;
      const options = this.options;
      if (isFunction(options.agRowClick) && !__ag_is_statistic_row__) {
        options.agRowClick(colDef, data, event.target);
      }
      this.$emit('on-row-click', params)
    },

    // 
    onRowDoubleClicked(params) {
      const { colDef, data, event } = params;
      const { __ag_is_statistic_row__ } = data;
      const options = this.options;
      if (isFunction(options.agRowDoubleClick) && !__ag_is_statistic_row__) {
        options.agRowDoubleClick(colDef, data, event.target);
      }
      this.$emit('on-row-dblclick', params)
    },

    // 
    onSelectionChanged(params) {
      const options = this.options;
      const ids = [];
      const rows = [];
      params.api.getSelectedRows().forEach((d) => {
        const { val } = d.ID;
        if (val !== this.$t('tips.summation') && val !== this.$t('tips.total')) {
          ids.push(val);
          rows.push(d);
        }
      });

      if (isFunction(options.agSelectionChanged)) {
        options.agSelectionChanged(ids, rows);
      }

      this.$emit('on-selection-change', rows)
    },

    // 
    onSortChanged(params) {
      const { api } = params;
      const options = this.options;
      const sortColArr = api.getSortModel()

      if (sortColArr.length !== 0) {
        api.showLoadingOverlay();
        // 
        if (!isFunction(options.agSortChanged)) {
          this.handleSort(sortColArr[0])
        }
        this.$emit('on-sort-change', sortColArr)
        api.hideOverlay()
      }

      if (isFunction(options.agSortChanged)) {
        options.agSortChanged(sortColArr);
      }
    },

    // 
    handleSort(sortInfo) {
      const colId = sortInfo.colId
      const data = this.data
      let newArr = []
      // 
      if (sortInfo.sort === 'desc') {
        newArr = data.sort((a, b) => {
          if (typeof (a[colId]) === 'string' || typeof (b[colId]) === 'string') {
            if (String(a[colId]) > String(b[colId])) {
              return -1
            } else {
              return 1
            }
          }
          return b[colId] - a[colId]
        })
      } else {
        // 
        newArr = data.sort((a, b) => {
          if (typeof (a[colId]) === 'string' || typeof (b[colId]) === 'string') {
            if (String(a[colId]) < String(b[colId])) {
              return -1
            } else {
              return 1
            }
          }
          return a[colId] - b[colId]
        })
      }

      // 
      this.columns.forEach(col => {
        // 
        if (col.sort) {
          col.sort = null
        }
        // 
        if (col.field === colId) {
          col.sort = sortInfo.sort
        }
      })
      this.api.setColumnDefs(this._transformColumnDefs(this.columns))
    },

    // 
    onVirtualColumnsChanged(params) {
      // const allVirtualCols = params.columnApi.getAllDisplayedVirtualColumns();
      // const currentLastVirtualColumn = allVirtualCols[allVirtualCols.length - 1].colId;
      // agGridTableContainer.setAttribute('data-last-virtual-column', currentLastVirtualColumn);
    },

    // 
    onColumnVisible(params) {
      const options = this.options;
      updateColumnVisibleDelay && clearTimeout(updateColumnVisibleDelay);
      updateColumnVisibleDelay = setTimeout(() => {
        if (isFunction(options.agColumnVisibleChanged)) {
          const hideColumns = [];
          params.columnApi.getColumnState().forEach((d) => {
            if (d.hide) {
              hideColumns.push(d.colId);
            }
          });
          options.agColumnVisibleChanged(hideColumns.toString(), params);
        }
      }, 10);
      this.$emit('on-column-visible', params)
    },

    // 
    onColumnMoved(param) {
      const { columnApi } = param;
      const options = this.options;
      updateColumnPositionDelay && clearTimeout(updateColumnPositionDelay);
      updateColumnPositionDelay = setTimeout(() => {
        if (isFunction(options.agColumnMoved)) {
          const columnState = columnApi.getColumnState().map(d => d.colId);
          const orderedColumns = [];
          const hideColumn = options.datas && options.datas.hideColumn && options.datas.hideColumn !== '' ? options.datas.hideColumn.split(',') : [];
          columnState.forEach((d) => {
            if (hideColumn.indexOf(d) === -1 && d !== 'ID') {
              // ID
              orderedColumns.push(d);
            }
          });
          // IDagTable
          this._colPosition = ['ID'].concat(orderedColumns).toString();
          options.agColumnMoved(['ID'].concat(orderedColumns).toString());
        }
      }, 500);
      this.$emit('on-column-moved', param)
    },

    // 
    //  
    onColumnPinned(params) {
      const options = this.options;
      const pinnedLeft = params.columnApi.getDisplayedLeftColumns().map(d => d.colId);
      const pinnedRight = params.columnApi.getDisplayedRightColumns().map(d => d.colId);
      const pinnedPosition = `${pinnedLeft.join(',')}|${pinnedRight.join(',')}`;

      if (isFunction(options.agColumnPinned)) {
        if (!this._isResetAllColumn) {
          if (params.pinned === null) {
            const columnState = params.columnApi.getColumnState().map(d => d.colId);
            const orderedColumns = [];
            const hideColumn = options.datas && options.datas.hideColumn && options.datas.hideColumn !== '' ? options.datas.hideColumn.split(',') : [];
            columnState.forEach((d) => {
              if (hideColumn.indexOf(d) === -1 && d !== params.column.colId && d !== 'ID') {
                // ID
                orderedColumns.push(d);
              }
            });

            //  
            // agTable.colPosition = orderedColumns.concat([params.column.colId]).toString();
            // options.agColumnMoved(orderedColumns.concat([params.column.colId]).toString());

            //  
            this._colPosition = ['ID'].concat([params.column.colId].concat(orderedColumns)).toString();
            setTimeout(() => {
              this._colPosition.split(',').forEach((d, i) => {
                params.columnApi.moveColumn(d, i);
              });
            }, 48);
            options.agColumnMoved && options.agColumnMoved(['ID'].concat([params.column.colId].concat(orderedColumns)).toString());
          }
        }
        //  
        if (!this._preventPinnedEmit) {
          options.agColumnPinned(pinnedPosition);
        }
      }
      this.$emit('on-column-pinned', params)
    },

    // api.setRowData()rowDatabound
    onRowDataChanged(params) {
      this.$emit('row-data-changed', params)
    },

    // 
    onBodyScroll(e) {
      // : 
      // // 
      // if (e.direction === 'horizontal') {
      //   if (this.resizeColumnsTimer) {
      //     clearTimeout(this.resizeColumnsTimer)
      //     this.resizeColumnsTimer = null
      //   }
      //   this.resizeColumnsTimer = setTimeout(() => {
      //     this._autoSizeColumns()
      //   }, 100)
      // }
      this.$emit('on-body-scroll', e)
    },

    //  
    getRowStyle(params) {
      const { rowHeight } = this.gridOptions
      if (rowHeight) {
        return {
          lineHeight: `${rowHeight}px`
        }
      }
      return { lineHeight: '28px' }
    },

    // 
    getRowClass(params) {
      let className = '';
      const { data } = params;
      const options = this.options;
      if (options.cssStatus) {
        options.cssStatus.forEach((d) => {
          if (data && this._zhColumnNameMap[d.desc]) {
            const columnName = this._zhColumnNameMap[d.desc]; // d.desc
            const columnValueOfCurrentRow = data[columnName].val; // d.desccolumnName
            className += `${d.css[d.value.indexOf(columnValueOfCurrentRow)] || ''} ` || ' ';
          }
        });
      }
      return className;
    },

    // 
    onFilterChanged(params) {
      this.$emit('on-filter-changed', params)
      const { api, columnApi } = params;
      const datas = this.options.datas
      if (!datas) {
        return
      }
      const { isFullRangeSubTotalEnabled, isSubTotalEnabled, subtotalRow } = datas

      if (!isFullRangeSubTotalEnabled && !isSubTotalEnabled) {
        return
      }

      // 
      const rows = api.getModel().rowsToDisplay.map(row => row.data)

      // 
      if (isSubTotalEnabled || isFullRangeSubTotalEnabled) {
        const subTotalResult = this._getSumOfEachColumn(Object.keys(subtotalRow), rows)
        Object.keys(subTotalResult).forEach(columnName => {
          const value = subTotalResult[columnName]
          if (this.rowDataType === 'string') {
            this.subtotalRowData[columnName] = toThousands(value) // 
          } else {
            this.subtotalRowData[columnName].val = toThousands(value) // 
          }
        })
      }

      // 
      const pinnedBottom = []
      if (isSubTotalEnabled) {
        pinnedBottom.push(this.subtotalRowData)
      }
      if (isFullRangeSubTotalEnabled) {
        pinnedBottom.push(this.fullRangeSubTotalRowData)
      }
      if (pinnedBottom.length > 0) {
        api.setPinnedBottomRowData(pinnedBottom)
      }
    },

    // 
    onColumnResized(params) {
      this.$emit('on-column-resized', params)
    },

    /**
     * -----------------------------start-------------------------ag
     */

    // 
    _fixAgRenderChoke() {
      const agTable = this.$refs.table
      const viewport = document.querySelector('.ag-body-viewport');
      if (viewport) {
        const currentViewPortScrollLeft = viewport.scrollLeft;
        viewport.scrollTop = 5;
        viewport.scrollLeft = 1;
        setTimeout(() => {
          viewport.scrollTop = 0;
          viewport.scrollLeft = currentViewPortScrollLeft || 0;
          this._resetColumnWidth()
        }, 100);
      }
      return agTable;
    },

    // 
    _getPinnedColumns(pinnedPosition) {
      let pinnedLeftColumns = []
      let pinnedRightColumns = []

      if (pinnedPosition) {
        //  ID, KEY | DATE, NUM  |  | 
        const pinnedColumns = pinnedPosition.split('|');
        pinnedLeftColumns = pinnedColumns[0].split(',');
        if (pinnedColumns[1]) {
          pinnedRightColumns = pinnedColumns[1].split(',');
        }
      }
      return {
        pinnedLeftColumns,
        pinnedRightColumns
      }
    },

    // 
    _sortPinnedColumns(visibleColumns) {
      const { pinnedLeftColumns, pinnedRightColumns } = this._getPinnedColumns(this.options.datas.pinnedColumns)
      let leftPoint = 0 // 
      let rightPoint = Math.max(visibleColumns.length - 1, 0) // 
      // visibleColumns.forEach((columnObj, index) => {
      //   if (index > rightPoint) {
      //     return
      //   }
      //   if (pinnedLeftColumns.includes(columnObj.colname) || columnObj.colname === 'ID') {
      //     // 
      //     const temp = visibleColumns[leftPoint]
      //     visibleColumns[leftPoint] = columnObj
      //     visibleColumns[index] = temp
      //     leftPoint++
      //   }
      //   if (pinnedRightColumns.includes(columnObj.colname)) {
      //     // 
      //     const temp = visibleColumns[rightPoint]
      //     visibleColumns[rightPoint] = columnObj
      //     visibleColumns[index] = temp
      //     rightPoint--
      //   }
      // })

      const tempColums = deepClone(visibleColumns)
      for (let index = 0; index < visibleColumns.length; index++) {
        if (index > rightPoint) {
          return
        }
        const columnObj = tempColums[index]
        const temp = columnObj
        if (pinnedLeftColumns.includes(columnObj.colname) || columnObj.colname === 'ID') {
          // 
          tempColums.splice(index, 1) // 
          tempColums.splice(leftPoint, 0, temp) // 
          leftPoint++
        }
        if (pinnedRightColumns.includes(columnObj.colname)) {
          tempColums.splice(index, 1)
          tempColums.splice(rightPoint, 0, temp)
          rightPoint--
        }
      }
      // 
      visibleColumns.forEach((item, index) => {
        visibleColumns[index] = tempColums[index]
      })
    },

    // 
    _getSortedData(data, colPosition) {
      // colPosition
      this._colPosition = colPosition;
      const positionColumns = colPosition.split(',');
      const visibleColumns = [];   // 
      const unVisibleColumns = [];  // 
      const columnMap = {};

      // 
      data.forEach((d) => {
        columnMap[d.colname] = d;
        if (positionColumns.indexOf(d.colname) === -1) {
          unVisibleColumns.push(d);
        }
      });

      // 
      positionColumns.forEach((columnName) => {
        if (columnMap[columnName]) {
          visibleColumns.push(columnMap[columnName]);
        }
      });

      // 
      if (this.options.datas && this.options.datas.pinnedColumns) {
        this._sortPinnedColumns(visibleColumns)
      }
      return visibleColumns.concat(unVisibleColumns)
    },

    // {val: xxx}xxx
    _getRowValue(value) {
      if (isObject(value)) {
        return value.val
      } else {
        return value
      }
    },

    // 
    _transformColumnDefs(data) {
      const agTable = this.$refs.table
      const options = this.gridOptions;
      const componentVM = this
      let hideColumn = [];
      if (options && options.datas && options.datas.hideColumn) {
        hideColumn = deepClone(options.datas.hideColumn.split(','))
      }

      // ----------  ---------------
      const defaultSortKeyMap = {};
      if (options && options.datas && options.datas.ordids && isArray(options.datas.ordids)) {
        options.datas.ordids.forEach((d) => {
          if (d.colname !== 'ID') {
            // ID
            defaultSortKeyMap[d.colname] = d.ordasc ? 'asc' : 'desc';
          }
        });
      }

      // ----------for menu tabs begin---------------
      const columnMenus = {
        generalMenuTab: options && options.generalMenuTab ? options.generalMenuTab : true, // generalMenuTab 
        filterMenuTab: options && options.filterMenuTab ? options.filterMenuTab : true, // filterMenuTab
        columnsMenuTab: options && options.columnsMenuTab ? options.columnsMenuTab : true, // columnsMenuTab
      };
      const menuTabs = [];
      ['generalMenuTab', 'filterMenuTab', 'columnsMenuTab'].forEach((tab) => {
        if (columnMenus[tab]) {
          menuTabs.push(tab);
        }
      });
      // ----------for menu tabs end---------------

      const newData = data.map((d) => {
        if (d.colname === 'headerName') {
          alert(`headerName : ${this.$t('table.columnNameConflict')}`);
        }
        if (d.colname === 'field') {
          alert(`field : ${this.$t('table.columnNameConflict')}`);
        }
        const item = deepClone(d);
        item._index = d._index;
        item.headerName = d.colname === 'ID' ? this.$t('tips.index') : d.headerName || d.name;
        item.lockVisible = d.lockVisible || d.colname === 'ID'; // 
        item.suppressToolPanel = d.suppressToolPanel || d.colname === 'ID'; // ID
        // item.pinned = d.colname === 'ID' ? 'left' : this._getPinnedState(d.colname); // 
        item.pinned = d.pinned || (d.colname === 'ID' ? 'left' : d.pinned); // 
        item.maxWidth = d.maxWidth || (d.colname === 'ID' ? 80 : null); // ID
        item.width = d.width || (d.webconf && d.webconf.standard_width ? Number(d.webconf.standard_width) : null); // ID
        // autoSizeAllColumns()widthwidthapi
        if (item.width) {
          item.maxWidth = item.width
          item.minWidth = item.width
        }

        item.suppressResize = d.suppressResize || d.colname === 'ID'; // ID
        item.suppressMovable = d.suppressMovable || d.colname === 'ID'; // ID
        item.lockPinned = d.lockPinned || true; // pinned
        item.lockPosition = d.lockPosition || d.colanme === 'ID'; //  ID
        item.headerComponentFramework = d.headerComponentFramework || (options && options.useDefaultHeader ? null : 'CustomHeader'); // useDefaultHeader = trueheaderComponent
        item.headerComponentParams = d.headerComponentParams || { agGridDiv: agTable.$el, tooltipBox, tooltipTopBox };
        item.field = item.field ? item.field : `${d.colname}.val`; // 
        // ID
        if (item.colname === 'ID') {
          item.field = `${this.sequenceColumnField}.val`
        }
        item.colId = d.colId || d.colname || d.field; // IDitem.field
        item.sort = d.sort || defaultSortKeyMap[d.colname]; // 
        item.filter = d.filter || d.isagfilter; // d.isfilter 
        if (d.agfilter === 'FUZZY' && d.isagfilter) {
          item.filter = 'agTextColumnFilter';
        }

        /**
         * 
         */
        const renderParams = this.renderParams && this.renderParams(item) || {} // renderParams
        // CellRenderByFunctionCellRenderCellRender,CellRenderByFunction
        // CellRenderByFunctionrendervue
        let cellRendererFramework = d.cellRendererFramework || (renderParams.renderContainer ? renderParams.renderContainer : 'CellRender')
        // 
        if (d.colname === 'ID' || this.sequenceColumnField === d.field) {
          cellRendererFramework = 'CellRender'
        }

        // renderComponent
        const cellRendererParams = d.cellRendererParams || {
          renderComponent: TextComponent, // 
          agGridDiv: agTable.$el,
          options,
          tooltipBox,
          tooltipTopBox,
          failIds: this._getFailIds(),
          mode: this.mode,
          $t: this.$t,
          ...renderParams
        };

        // cellRendererSelector
        if (!d.cellRendererSelector) {
          item.cellRendererSelector = params => {
            const defaultRender = {
              component: cellRendererFramework,
              params: {
                ...cellRendererParams,
                // colnameID
                renderComponent: params.colDef.colname === 'ID' || this.sequenceColumnField === params.colDef.field ? SequenceComponent : cellRendererParams.renderComponent
              }
            }
            let cellValue = this._getRowValue(params.data[this.sequenceColumnField])
            // const cellValue = params.data[this.sequenceColumnField] && params.data[this.sequenceColumnField].val
            // 
            if ((cellValue === this.$t('tips.summation') || cellValue === this.$t('tips.total'))) {
              return {
                component: 'CellRender',
                params: {
                  ...cellRendererParams,
                  ...params,
                  renderComponent: TextComponent
                }
              }
            }
            return defaultRender
          }
        }

        // 
        // item.pinnedRowCellRendererFramework = d.pinnedRowCellRendererFramework || 'CellRender'
        // item.pinnedRowCellRendererParams = d.pinnedRowCellRendererParams || {
        //   renderComponent: TextComponent
        // }
        // cell rendering agGridDiv 
        item.sortingOrder = d.sortingOrder || (item.isorder ? ['asc', 'desc'] : [null]); // 
        item.unSortIcon = d.unSortIcon || item.isorder; // Icon
        item.hide = d.hide || hideColumn.includes(item.colname);
        item.suppressMenu = d.suppressMenu || d.colname === 'ID'; // 
        item.checkboxSelection = d.colname === 'ID' ? function (params) {
          return params.columnApi.getRowGroupColumns().length === 0 && (params.data.ID && params.data.ID.val !== componentVM.$t('tips.summation') && params.data.ID.val !== componentVM.$t('tips.total'));
        } : d.checkboxSelection;
        item.headerCheckboxSelection = d.colname === 'ID' ? function (params) {
          return params.columnApi.getRowGroupColumns().length === 0;
        } : d.checkboxSelection;
        item.headerCheckboxSelectionFilteredOnly = d.headerCheckboxSelectionFilteredOnly || d.colname === 'ID';
        item.cellClass = d.cellClass || function (params) {
          const { colDef } = params;
          const { colId, align, tdAlign } = colDef;
          const cellAlign = tdAlign || align || 'center' // 
          if (colId === 'ID') {
            return null
          } else {
            return cssFeatures[`table-cell-${cellAlign}`];
          }
        };
        // type=Number, display=text 
        // zhColumnNameMap
        this._zhColumnNameMap[item.headerName] = d.colname;
        return item;
      });

      return newData
    },

    // 
    _transformRowData(data) {
      const options = this.gridOptions;
      const ids = (options.datas && options.datas.ids) || '';
      const keySetOfIdSequence = {};
      let rowData = [];
      if (isArray(ids) && ids.length > 0) {
        ids.forEach((d, i) => {
          keySetOfIdSequence[d] = parseInt(i + 1, 10);
        });
        rowData = data.concat([]).sort((a, b) => (keySetOfIdSequence[a.ID.val] || Infinity) - (keySetOfIdSequence[b.ID.val] || Infinity)); // idsrowData
      } else {
        rowData = data;
      }
      const pinnedBottomRowData = [];
      let fullRangeSubTotalRow = null;
      let isFullRangeSubTotalEnabled = null;
      let isSubTotalEnabled = null;
      let subtotalRow = null
      if (options.datas) {
        const { datas } = options;
        fullRangeSubTotalRow = datas.fullRangeSubTotalRow;
        isFullRangeSubTotalEnabled = datas.isFullRangeSubTotalEnabled;
        isSubTotalEnabled = datas.isSubTotalEnabled;
        subtotalRow = datas.subtotalRow
      }

      // 
      if (isSubTotalEnabled) {
        this.subtotalRowData = this._dealWithSubTotalRow(subtotalRow)
        pinnedBottomRowData.push(this.subtotalRowData);
      }

      // 
      if (isFullRangeSubTotalEnabled && fullRangeSubTotalRow) {
        this.fullRangeSubTotalRowData = this._dealWithFullRangeSubTotalRow(fullRangeSubTotalRow)
        pinnedBottomRowData.push(this.fullRangeSubTotalRowData);
      }

      // 
      if (this.mode === Common_Table_Mode) {
        for (let row of pinnedBottomRowData) {
          let staticRow = row.__ag_is_statistic_row__
          for (const key in row) {
            row[key] = row[key].val;
          }
          row[this.sequenceColumnField] = {
            val: row.ID
          }
          row.__ag_is_statistic_row__ = staticRow;
        }
      }

      return { rowData, pinnedBottomRowData };
    },

    // 
    _dealWithFullRangeSubTotalRow(fullRangeSubTotalRow) {
      let fullRangeSubTotalRowData = {};
      this.columnApi.getAllColumns().forEach((d) => {
        const colname = d.colDef.colname || d.colDef.field;
        const originFullRangeSubTotalRow = isObject(fullRangeSubTotalRow[colname]) ? fullRangeSubTotalRow[colname].val : fullRangeSubTotalRow[colname] // 
        const subTotal = originFullRangeSubTotalRow !== undefined && originFullRangeSubTotalRow !== null ? originFullRangeSubTotalRow : ''
        fullRangeSubTotalRowData[colname] = { val: colname === 'ID' ? this.$t('tips.total') : subTotal };
      });
      if (JSON.stringify(fullRangeSubTotalRow) !== '{}') {
        fullRangeSubTotalRowData.__ag_is_statistic_row__ = true;
      }
      fullRangeSubTotalRowData[this.sequenceColumnField] = {}
      fullRangeSubTotalRowData[this.sequenceColumnField].val = this.$t('tips.total')

      // {val: xxx}  xxx
      if (this.rowDataType === 'string') {
        fullRangeSubTotalRowData = this._objectToString(fullRangeSubTotalRowData)
      }

      return fullRangeSubTotalRowData;
    },

    // key
    // 12
    _getSumOfEachColumn(columns, rows) {
      const result = {}
      let subtotal = 0
      columns.forEach(fieldName => {
        // 
        const currentColumn = this.columnApi.getAllColumns().find(d => d.colId === fieldName)
        const scale = currentColumn.colDef.scale || 0 // 
        subtotal = rows.reduce((sum, item) => {
          let value = undefined
          if (this.rowDataType === 'string') {
            value = item[fieldName] || 0
          } else {
            value = item[fieldName].val || 0
          }
          return sum + Number(value)
        }, 0)
        subtotal = subtotal.toFixed(scale)
        result[fieldName] = subtotal
      })
      return result
    },

    // 
    _dealWithSubTotalRow(subtotalRow) {
      let subtotalRowData = {};
      // 
      this.columnApi.getAllColumns().forEach((d) => {
        const colname = d.colDef.colname || d.colDef.field;
        const originSubtotalRow = isObject(subtotalRow[colname]) ? subtotalRow[colname].val : subtotalRow[colname] // 
        const val = originSubtotalRow !== undefined && originSubtotalRow !== null ? originSubtotalRow : ''
        subtotalRowData[colname] = { val: colname === 'ID' ? this.$t('tips.summation') : val };
      });
      subtotalRowData.__ag_is_statistic_row__ = true;
      subtotalRowData[this.sequenceColumnField] = {}
      subtotalRowData[this.sequenceColumnField].val = this.$t('tips.summation')

      // {val: xxx}  xxx
      if (this.rowDataType === 'string') {
        subtotalRowData = this._objectToString(subtotalRowData)
      }
      return subtotalRowData
    },

    // val
    // a:{val: xxx}  a:xx
    _objectToString(obj) {
      const newObj = deepClone(obj)
      Object.keys(newObj).forEach(key => {
        const fieldVal = newObj[key]
        if (isObject(fieldVal)) {
          newObj[key] = fieldVal.val
        }
      })
      return newObj
    },

    // deleteFailInfo
    _getFailIds() {
      const options = this.gridOptions;
      const failIds = [];
      if (options && options.datas && options.datas.deleteFailInfo && isArray(options.datas.deleteFailInfo)) {
        options.datas.deleteFailInfo.forEach((d) => {
          if (d.objid && d.objid !== '') {
            failIds.push(`${d.objid}`);
          }
        });
        return failIds;
      }
    },

    // ag-body-viewport 
    _horizontalScrollTo(element, scrollValue) {
      element.scrollLeft = parseFloat(scrollValue);
      this._autoSizeColumns()
    },

    // fix: 
    _resetHeaderPosition() {
      const agGridTableContainer = this.$refs.tableContainer
      if (agGridTableContainer) {
        const header = agGridTableContainer.querySelector('.ag-header-container')
        header.style.left = 0
      }
    },

    // 
    // 1.autoSizeAllColumns  2.sizeColumnsToFit
    _autoSizeColumns() {
      // 
      if (!this._isActive || !this.$refs.table || !this.api || !this.columnApi) {
        return
      }
      let viewport
      let container
      const tableDom = this.$refs.table.$el
      // oms
      if (this.data.length === 0) {
        viewport = tableDom.querySelector('.ag-header-viewport') // ,
        container = tableDom.querySelector('.ag-header-container>.ag-header-row') // 
      } else {
        viewport = tableDom.querySelector('.ag-center-cols-clipper') // ,
        container = tableDom.querySelector('.ag-center-cols-container') // 
      }

      let viewportWidth = viewport.clientWidth
      let containerWidth = container.clientWidth
      if (containerWidth === 0 && viewportWidth === 0) {
        if (this._viewportWidth === 0 && this._containerWidth === 0) {
          return
        } else {
          viewportWidth = this._viewportWidth
          containerWidth = this._containerWidth
        }
      }
      this._viewportWidth = viewportWidth // 
      this._containerWidth = containerWidth // 

      if (containerWidth <= viewportWidth) {
        this.api.sizeColumnsToFit()
      } else {
        this.columnApi.autoSizeAllColumns()
      }
    },

    // 
    _resetColumnWidth(callback) {
      // fix: ,
      // autoSizeAllColumns
      this._autoSizeColumns()
      setTimeout(() => {
        this._autoSizeColumns()
        callback && callback()
      }, 200)
    },
  },

  beforeCreate() {
    this.$t = i18n.t.bind(i18n)
  },

  created() {
    this._zhColumnNameMap = {}; // { '': 'english_name' }
    this._colPosition = '' // ,
    this._originColumnDefs = null // ,
    this._preventPinnedEmit = null // 
    this._isResetAllColumn = false // 
    this._pinnedBottomRowData = null
    this._gridReady = false // 
    this._tableWidthCache = 0 // 
    this._viewportWidth = 0 // 
    this._containerWidth = 0 // 
    this._isActive = true // 
    this._clickTime = undefined // 
  },

  mounted() {
  },

  activated() {
    this._isActive = true
    this.$nextTick(() => {
      setTimeout(() => {
        if (isArray(this.data) && this.data.length > 0) {
          this._resetColumnWidth(this._resetHeaderPosition)
        }
      }, 500)

      // fix:
      const dom = document.querySelector('.ag-grid-table-wrap .tooltip-box')
      if(!dom) {
        const agGridDiv = this.$refs.table.$el
        agGridDiv.appendChild(tooltipBox)
      }
    })
  },

  deactivated() {
    this._isActive = false
  }
}
</script>

